    <section class="page-action">

        <div class="container-fluid p-0">
            <div class="row">
                <div class="col-md-12 padding-t_8">

                    <div class="theme-primary partition p-actions-expand tablehead">
                        <span class="p-name text-white">{{ $t("HelpArticles") }}</span>
                        <router-link to="/Help/HelpAndGuide"><span class="p-actions"><a title="" class="p-action-btn text-white float-right" data-toggle-tooltip="tooltip" data-original-title="Back">
                                    <em class="fa fa-angle-double-left"></em><br />{{ $t("Back") }}</a>

                            </span> </router-link>
                    </div>
                </div>
            </div>
        </div>
        <h5 class="font-weight-normal text-center my-4">Where would you like to Start?</h5>

        <div class="row pt-2 p-0 mx-auto justify-center">
            <div class="col-sm-12 col-md-3 col-lg-3 col-xl-3"></div>

            <div class="col-sm-12 col-md-12 col-lg-5 col-xl-5 float-left Search-panel float-left p-0" style="position:relative;">

                <vue-bootstrap-typeahead :data="ArticleNameList" />                

            </div>
            <div class="col-4 float-left pl-2">
                <div class="background-0 text-left form-check-inline">
                    <a href="javascript:;" class="btn btn-success mr-1 px-4" data-toggle-tooltip="tooltip" data-original-title="Search" @click="SearchData(ArticleListData)"><em class="fa fa-search"></em></a>
                    <a id="Clear" href="javascript:;" title="" class="btn btn-danger clearsearchtext px-4" data-toggle-tooltip="tooltip" data-original-title="Clear" @click="ClearData"><em class="fa fa-refresh"></em></a>
                </div>
            </div>
        </div>

        <div class="row">
            <div class="col-12 madule-list text-center">
                <router-link to="/Help/HelpArticlesPerModule"><a class="text-primary"><u>View Module List</u></a>
                </router-link>
            </div>
        </div>
        <div class="row" v-if="IsPopularSearchShow">
            <div class="col-md-12">
                <div class="text-center my-4">
                    <h5>Popular Searches</h5>
                    <h6>
                        Loream ipsum dolor sit amet, consectetur adipiscing elit.
                        Phasellus dui neque, pulvinar id porta sed, condimentum a lacus
                    </h6>
                </div>
            </div>
        </div>

        <div class="row" v-if="IsPopularSearchShow">
            <div class="col-md-3">
                <div class="border py-4 px-3">
                    <div class="row d-flex align-items-center">
                        <div class="col-md-2">
                            <div class="text-center equator-color"><em class="fa fa-clock-o font-36" aria-hidden="true"></em></div>
                        </div>
                        <div class="col-md-9">
                            <h5 class="card-title mb-0 font-18">How to Setup Time Zone?</h5>
                        </div>
                        <div class="col-md-1">
                            <div class="text-center equator-color font-weight-light text-secondary font-28"><a class="" href="#"><em class="fa fa-angle-right text-secondary" aria-hidden="true"></em></a>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

            <div class="col-md-3">
                <div class="border py-4 px-3">
                    <div class="row d-flex align-items-center">
                        <div class="col-md-2">
                            <div class="text-center equator-color"><em class="fa fa-user-plus text-info font-36" aria-hidden="true"></em></div>
                        </div>
                        <div class="col-md-9">
                            <h5 class="card-title mb-0 font-18">How to Add User?</h5>
                        </div>
                        <div class="col-md-1">
                            <div class="text-center equator-color font-weight-light text-secondary font-28"><a class="" href="#"><em class="fa fa-angle-right text-secondary" aria-hidden="true"></em></a>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

            <div class="col-md-3">
                <div class="border py-4 px-3">
                    <div class="row d-flex align-items-center">
                        <div class="col-md-2">
                            <div class="text-center equator-color"><em class="fa fa-random text-warning font-36" aria-hidden="true"></em></div>
                        </div>
                        <div class="col-md-9">
                            <h5 class="card-title mb-0 font-18">How to Setup Shifts?</h5>
                        </div>
                        <div class="col-md-1">
                            <div class="text-center equator-color font-weight-light text-secondary font-28"><a class="" href="#"><em class="fa fa-angle-right text-secondary" aria-hidden="true"></em></a>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

            <div class="col-md-3">
                <div class="border py-4 px-3">
                    <div class="row d-flex align-items-center">
                        <div class="col-md-2">
                            <div class="text-center equator-color"><em class="fa fa-map-marker text-primary font-36" aria-hidden="true"></em></div>
                        </div>
                        <div class="col-md-9">
                            <h5 class="card-title mb-0 font-18">How to Add Locations?</h5>
                        </div>
                        <div class="col-md-1">
                            <div class="text-center equator-color font-weight-light text-secondary font-28"><a class="" href="#"><em class="fa fa-angle-right text-secondary" aria-hidden="true"></em></a>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

        </div>

        <div class="accordion accordion-flush" :id="'accordionFlushExample_' + item.articleId" v-if="IsAccordionShow" v-for="(item, index) in HelpArticleList" :key="index">
            <div class="accordion-item">

                <ul>
                    <li class="accordion-header" :id="'flush-headingOne_' + item.articleId" style="margin-left:15px;">
                        <a class="collapsed" data-bs-toggle="collapse" :data-bs-target="'#flush-collapseOne_' + item.articleId" aria-expanded="false" :aria-controls="'flush-collapseOne_' + item.articleId" @click="GetArticleContent(item.articleId)">{{ item.articleName }}</a>
                    </li>

                </ul>

                <div class="col-12 p-0">
                    <div :id="'flush-collapseOne_' + item.articleId" class="accordion-collapse collapse p-0 border" :aria-labelledby="'flush-headingOne_' + item.articleId" :data-bs-parent="'#accordionFlushExample_' + item.articleId">
                        <div class="accordion-body ArticleList p-2" v-html="ArticlesContent">

                        </div>
                    </div>
                </div>
            </div>

        </div>

        <div v-if="IsDataNotFoundShow">
            <div class="col-md-12">
                <div class="border text-center text-danger no-record mt-4 p-2">
                    No Record Found
                </div>
            </div>
        </div>

    </section>
</div>
</template>

<script>
import Search from "../../../src/views/HelpAndGuide/Search.vue";
import DataService from '../../services/DataService';

import {storeToRefs} from "pinia";
import {useloggedUserInfo} from "@/stores/UserInfo";
const {GetUserInfo} = storeToRefs(useloggedUserInfo());

import VueBootstrapTypeahead from 'vue-bootstrap-typeahead'
//import 'bootstrap/scss/bootstrap.scss'

export default {
    name: 'HelpArticles',

    components: {
        Search,
        VueBootstrapTypeahead,

    },
    data() {
        return {
            HelpArticleList: [],
            ArticleNameList: [],
            Article: '',
            ArticlesContent: '',
            IsPopularSearchShow: true,
            IsDataNotFoundShow: false,
            IsAccordionShow: false,
            isLoading: false,
            Path: Boolean,
            NewValue: Boolean,
            UserType: "",
            
        }
    },
    created: function () {

        this.UserType = GetUserInfo.value.UserType;
        this.ArticleList();

    },
    methods: {

        ArticleList: function () {
            var vm = this;

            vm.isLoading = true;

            var params = `search=&userType=${GetUserInfo.value.UserType}&articleType=HelpArt&moduleId=`

            DataService.GetArticleList(params).then(function (response) {

                if (response.data.length > 0) {

                    response.data.forEach((value, index) => {

                        vm.ArticleNameList.push(value.articleName)
                        vm.isLoading = false;
                    })
                }

            });
        },
        SearchData: function () {

            var vm = this;
            vm.isLoading = true;
            vm.Article = document.getElementById("typeahead_id").value;

            var params = `search=${vm.Article}&userType=${GetUserInfo.value.UserType}&articleType=HelpArt&moduleId=`;
            DataService.GetArticleList(params).then(function (response) {

                if (response.data.length > 0) {
                    vm.HelpArticleList = response.data;
                    vm.IsPopularSearchShow = false;
                    vm.IsAccordionShow = true;
                    vm.isLoading = false;
                } else if (response.data.length == 0) {

                    vm.IsPopularSearchShow = false;
                    vm.IsDataNotFoundShow = true;
                    vm.IsAccordionShow = false;
                }

            });
        },
        ClearData: function () {
            document.getElementById("typeahead_id").value = '';
            this.IsPopularSearchShow = true;
            this.IsDataNotFoundShow = false;
            this.IsAccordionShow = false;

        },
        GetArticleContent: function (ArticlesId) {
            var vm = this;
            var params = `ArticleId=${ArticlesId}&articleType=HelpArt`;
            DataService.GetArticleDetailById(params).then(function (response) {
                if (response.data != null) {
                    vm.ArticlesContent = response.data.article_content;
                }
            });
        },
    },
};
</script>

<style>
.ArticleList {
    max-height: 400px;
    overflow-y: auto;
    position: relative;
}

#typeahead_id {
    width: 100%;
}

#typeahead_id_wrapper .simple-typeahead-list {
    text-align: left;
    width: 100%;

}
</style>
